.row {
    color: white;
    margin-left: 20px;
}

.row__poster {
    width: 100%;
    object-fit: contain;
    max-height: 150px;
    transition: transform 450ms;
    margin-right: 10px;
}

.row__posters {
    display: flex;
    overflow-y: hidden;
    overflow-x: scroll;
    padding: 20px;
}
.row__posters::-webkit-scrollbar {
    display: none;
}

.row__poster:hover {
    transform: scale(1.10);
}

.row__posterLarge {
    max-height: 250px;
}

.row__posterLarge:hover {
    transform: scale(1.20);
}